import { axisBottom } from 'd3-axis'
import { ResizeObserver } from '@juggle/resize-observer'
import { type BrushBehavior, brushX } from 'd3-brush'
import { scaleLinear, scaleSymlog } from 'd3-scale'
import { type Selection, select } from 'd3-selection'
import { extent, rollup, pairs } from 'd3-array'
import { HistogramConfig, type HistogramConfigInterface, type HistogramEvents } from './config'
import { getCountsInRange } from '../../utils'
import type { BarData } from './types'

import s from './style.module.css'

export class Histogram {
  private _config = new HistogramConfig()

  private _svg: SVGElement
  private _containerNode: HTMLElement
  private _noDataDiv: HTMLElement
  private _resizeObserver: ResizeObserver
  private _axisGroup: Selection<SVGGElement, unknown, null, undefined>
  private _barsGroup: Selection<SVGGElement, unknown, null, undefined>
  private _highlightedBarsGroup: Selection<SVGGElement, unknown, null, undefined>
  private _brushGroup: Selection<SVGGElement, unknown, null, undefined>
  private _firstRender = true

  private _formatter: ((n: number) => string) | undefined
  private _height: number
  private _width: number
  private _histogramWidth: number
  private _histogramHeight: number

  private _barWidth = 0
  private _maxCount = 0

  private _extent: [number, number] | undefined
  private _barsData: BarData[] = []
  private _highlightedBarsData: BarData[] = []
  private _histogramData?: number[] = undefined
  private _highlightedData?: number[] = undefined
  private _bandIntervals: number[] = []
  private _calculatedStep = 0
  private _currentSelection: [number, number] | undefined
  private _currentSelectionInPixels: [number, number] | undefined

  private _yScale = scaleSymlog<number, number, never>()
  private _xScale = scaleLinear<number, number, never>()

  private _axis = axisBottom<number>(this._xScale)
  private _brushInstance!: BrushBehavior<unknown> | undefined

  public constructor (containerNode: HTMLElement, config?: HistogramConfigInterface) {
    const { offsetWidth, offsetHeight } = containerNode
    if (config) this._config.init(config)
    this._containerNode = containerNode
    this._containerNode.classList.add(s.histogram)
    this._width = offsetWidth
    this._height = offsetHeight
    this._formatter = config?.formatter ?? undefined
    this._histogramWidth = this._width - this._config.padding.left - this._config.padding.right
    this._histogramHeight = this._height - this._config.padding.top - this._config.padding.bottom

    this._svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg')
    this._svg.classList.add(s.histogramSvg)
    this._containerNode.appendChild(this._svg)

    this._noDataDiv = document.createElement('div')
    select(this._noDataDiv).style('display', 'none').attr('class', s.noData).append('div').text('No histogram data')
    this._containerNode.appendChild(this._noDataDiv)

    this._axisGroup = select(this._svg).append('g').attr('class', s.axis)
    this._barsGroup = select(this._svg).append('g').attr('class', s.bars)
    this._highlightedBarsGroup = select(this._svg).append('g').attr('class', s.bars)
    this._brushGroup = select(this._svg).append('g').attr('class', s.brush)
    this._axis.tickFormat((d) => (this._formatter ? this._formatter(d) : d.toFixed(0)))

    this._resizeObserver = new ResizeObserver(() => {
      this.resize()
    })
    this._resizeObserver.observe(this._containerNode)
  }

  private get _barPadding (): number {
    return this._barWidth * this._config.barPadding
  }

  /**  `getCurrentSelection`: Returns current brush selection. */
  public get getCurrentSelection (): number[] | undefined {
    return this._currentSelection
  }

  /**  `getCurrentSelectionInPixels`: Returns current brush selection in pixels. */
  public get getCurrentSelectionInPixels (): number[] {
    return this._currentSelectionInPixels ?? []
  }

  /**  `getBarWidth`: Returns computed bar width in pixels */
  public getBarWidth (): number {
    return this._barWidth - this._barPadding
  }

  /**  `getConfig`: Returns current `Histogram` configuration */
  public getConfig (): HistogramConfig {
    return this._config
  }

  /**  `setConfig`: Function for setting config of `Histogram`. */
  public setConfig (config?: HistogramConfigInterface): void {
    const prevConfig = JSON.parse(JSON.stringify(this._config))
    if (!config) {
      this._config = new HistogramConfig()
    } else {
      this._config.init(config)
    }
    if (!this._config.allowSelection) {
      this._disableBrush()
    }
    // Data-related props
    if (this._config?.dataStep !== prevConfig.config?.dataStep || this._config?.barCount !== prevConfig.config?.barCount) {
      this._updateHistogramData()
      this._updateHistogramHighlightedData()
    }
    this.resize()
  }

  /**  `setHistogramData`: Function for setting data of `Histogram`. */
  public setHistogramData (data: number[] | undefined, customExtent?: [number, number]): void {
    if (customExtent && customExtent[1] <= customExtent[0]) return
    this._histogramData = data?.filter((d) => !isNaN(d) && d !== undefined)
    this._highlightedBarsData = []
    this._currentSelection = undefined
    this._brushCurrentSelection()
    this._config.events.onBrush?.(this._currentSelection)
    select(this._noDataDiv).style('display', 'none')
    if (this._histogramData?.length) {
      this._extent = customExtent || (extent(this._histogramData) as [number, number])
      this._firstRender = true
      this._updateHistogramData()
      this._updateScales()
      this.render()
    } else {
      select(this._noDataDiv).style('display', 'block')
    }
  }

  /**  `setHighlightedData`: Function for setting highlighted data of `Histogram`. */
  public setHighlightedData (data: number[] | undefined): void {
    if (!this._histogramData) return
    if (!data || data.length === 0) {
      const prevData = JSON.stringify(this._highlightedBarsData)
      this._highlightedData = []
      this._highlightedBarsData = []
      if (prevData !== JSON.stringify(this._highlightedBarsData)) {
        this._updateBars(true)
      }
    } else {
      this._highlightedData = data?.filter((d) => !isNaN(d) && d !== undefined)
      this._updateHistogramHighlightedData()
      this._updateBars(true)
    }
  }

  /**  `setSelection`: Set the selected range on a `Histogram`. Takes a numeric selection range in the X axis u nits as a parameter. */
  public setSelection (selection?: [number, number], renderOnly = false): void {
    const prevSelection = this._currentSelection
    if (selection && this._extent && selection[0] >= this._extent[0] && selection[1] <= this._extent[1] && selection[0] < selection[1]) {
      const selectionInPixels = selection.map(this._xScale) as [number, number]
      this._mapSelection(selectionInPixels)
    } else {
      this._currentSelection = undefined
    }
    const { _currentSelection } = this
    if (!renderOnly && (prevSelection?.[0] !== _currentSelection?.[0] || prevSelection?.[1] !== _currentSelection?.[1])) {
      this._config.events.onBrush?.(this._currentSelection, true)
    }
    this._brushCurrentSelection()
  }

  /**  `resize`: Resizes `Histogram` according to the parent node attributes. */
  public resize (): void {
    const { offsetWidth, offsetHeight } = this._containerNode
    this._width = offsetWidth
    this._height = offsetHeight
    this._histogramWidth = this._width - this._config.padding.left - this._config.padding.right
    this._histogramHeight = this._height - this._config.padding.top - this._config.padding.bottom
    if (this._histogramHeight > this._config.padding.top + this._config.padding.bottom) {
      this._updateScales()
      if (this._currentSelection) this.setSelection(this._currentSelection, true)
      if (!this._firstRender) this.render()
    }
  }

  /**  `render`: Renders `Histogram`. */
  public render (): void {
    this._updateBrush()
    this._updateBars()
    this._updateBars(true)
    this._updateAxis()
    if (this._firstRender) this._firstRender = false
  }

  public destroy (): void {
    this._containerNode.innerHTML = ''
  }

  private _updateAxis (): void {
    if (!this._histogramData) return
    this._axisGroup
      .style('transform', `translate(${this._config.padding.left}px, ${this._config.padding.top - this._config.selectionPadding / 2 + 1}px)`)
      .call(this._axis)
      .call((g) => g.select('.domain').remove())

    this._axisGroup.selectAll('.tick').select('text').attr('class', s.axisTick).attr('y', 0).attr('dy', 0).attr('dx', this._config.labelSideMargin)

    this._axisGroup.selectAll('.tick:last-of-type text').attr('dx', -this._config.labelSideMargin).style('text-anchor', 'end')

    this._axisGroup.selectAll('line').attr('y2', this._histogramHeight).attr('y1', 0).attr('opacity', 0.1)
  }

  private _updateBrush (): void {
    if (!this._config.allowSelection) return
    this._brushGroup.style('transform', `translate(${this._config.padding.left}px, ${this._config.padding.top}px)`)
    this._brushInstance = brushX().extent([
      [0, 0],
      [this._histogramWidth, this._histogramHeight],
    ])
    this._brushInstance.on('end', ({ selection, sourceEvent }: { selection: [number, number]; sourceEvent: MouseEvent }) => {
      if (!sourceEvent) return
      if (selection) {
        this._mapSelection(selection)
        this._config.events.onBrush?.(this._currentSelection)
      } else {
        this._currentSelection = undefined
        this._config.events.onBrush?.(undefined)
      }
      this._brushCurrentSelection()
    })
    this._brushGroup.call(this._brushInstance)
    this._brushGroup.select('rect.selection').classed(s.selection, true).attr('rx', this._config.selectionRadius).attr('ry', this._config.selectionRadius)
  }

  private _updateBars (highlighted?: boolean): void {
    const target = highlighted ? this._highlightedBarsGroup : this._barsGroup
    target.style('transform', `translate(${this._config.padding.left}px, ${this._config.padding.top - this._config.selectionPadding / 2}px)`)
    const opacity = highlighted ? 0.5 : 1
    const bars = target
      .selectAll(`.${highlighted ? s.highlightedBar : s.bar}`)
      .data(highlighted ? this._highlightedBarsData : this._barsData)
      .join('rect')
      .attr('class', highlighted ? s.highlightedBar : s.bar)
      .attr('x', (d) => this._xScale(d.rangeStart) + this._barPadding / 2)
      .attr('width', this.getBarWidth())
      .attr('rx', this._config.barRadius)
      .attr('ry', this._config.barRadius)
      .attr('y', -this._histogramHeight)

    if (!highlighted) {
      if (this._config.events.onBarHover) bars.on('mouseover', this._config.events.onBarHover)
    }

    bars
      .transition()
      .duration(300)
      .attr('height', (d) => {
        // Do not apply `minBarHeight` for highlighted layer
        if (highlighted) return d.count === 0 ? 0 : this._yScale(d.count)
        else return this._yScale(d.count)
      })
      .style('opacity', (d) => (this._yScale(d.count) === this._config.minBarHeight ? 0.4 : opacity))
  }

  private _updateScales (): void {
    if (!this._extent || !this._barsData.length) return
    const lastBar = this._barsData[this._barsData.length - 1] as BarData
    const precisedExtent = this._config.dataStep ? [this._extent[0], lastBar.rangeEnd] : this._extent
    this._xScale.domain(precisedExtent).range([0, this._histogramWidth]).clamp(true)
    this._yScale
      .range([this._config.minBarHeight, this._histogramHeight - this._config.barTopMargin - this._config.selectionPadding])
      .domain([0, this._maxCount])
      .clamp(true)
    this._axis.tickValues(precisedExtent)
    this._barWidth = this._xScale(lastBar.rangeEnd) - this._xScale(lastBar.rangeStart)
  }

  private _disableBrush (): void {
    this._currentSelection = undefined
    this._brushCurrentSelection()
    this._brushGroup.selectAll('*').remove()
  }

  private _updateHistogramData (): void {
    if (this._histogramData?.length && this._extent) {
      const countedNumbers = rollup(
        this._histogramData,
        (v) => v.length,
        (d) => d
      )
      const step = this._config.dataStep ?? Math.abs(this._extent[1] - this._extent[0]) / (this._config.barCount - 1)
      if (step === 0) return
      else this._calculatedStep = step
      this._bandIntervals = this._generateSequence(this._extent[0], this._extent[1], this._calculatedStep)
      if (this._config.dataStep) {
        const lastTick = this._bandIntervals[this._bandIntervals.length - 1] as number
        if (lastTick < this._extent[1]) {
          this._bandIntervals.push(+lastTick + this._calculatedStep)
        }
      }
      const valuePairs = pairs(this._bandIntervals)
      this._barsData = valuePairs.map((d) => ({
        rangeStart: d[0],
        rangeEnd: d[1],
        count: getCountsInRange(countedNumbers, d),
      }))
      this._maxCount = Math.max(...this._barsData.map((d) => d.count))
    }
  }

  private _updateHistogramHighlightedData (): void {
    if (this._highlightedData?.length && this._extent) {
      const countedNumbers = rollup(
        this._highlightedData,
        (v) => v.length,
        (d) => d
      )
      this._bandIntervals = this._generateSequence(this._extent[0], this._extent[1], this._calculatedStep)
      const valuePairs = pairs(this._bandIntervals)
      this._highlightedBarsData = valuePairs.map((d, i) => {
        let count = getCountsInRange(countedNumbers, d)
        const targetBar = this._barsData[i] as BarData
        if (targetBar) {
          if (count > (this._barsData[i] as BarData).count) count = (this._barsData[i] as BarData).count
        }
        return {
          rangeStart: targetBar.rangeStart,
          rangeEnd: targetBar.rangeEnd,
          count,
        }
      })
    }
  }

  private _mapSelection (selectionInPx: [number, number]): void {
    if (!this._barsData.length) return
    if (this._config.stickySelection) {
      this._currentSelection = [this._getClosestRange(this._xScale.invert(selectionInPx[0])).rangeStart, this._getClosestRange(this._xScale.invert(selectionInPx[1]), true).rangeEnd]
      if (this._currentSelection[0] === this._currentSelection[1]) this._currentSelection = undefined
    } else {
      this._currentSelection = selectionInPx.map(this._xScale.invert) as [number, number]
    }
  }

  private _brushCurrentSelection (): void {
    if (this._currentSelection) {
      this._currentSelectionInPixels = this._currentSelection.map(this._xScale) as [number, number]
      if (this._brushInstance && !this._firstRender) this._brushGroup.call(this._brushInstance.move, this._currentSelectionInPixels)
    } else {
      this._currentSelectionInPixels = undefined
      this._brushInstance?.clear(this._brushGroup)
    }
  }

  private _generateSequence (start: number, end: number, step: number): number[] {
    const count = Math.round((end - start) / step) + 1
    const arr = new Array(count)
    for (let i = 0; i < count; i++) {
      arr[i] = start + i * step
    }
    return arr
  }

  private _getClosestRange = (goal: number, end?: boolean): BarData =>
    this._barsData?.reduce((prev, curr) => (Math.abs(curr[end ? 'rangeEnd' : 'rangeStart'] - goal) < Math.abs(prev[end ? 'rangeEnd' : 'rangeStart'] - goal) ? curr : prev))
}

export { HistogramConfig }
export type { HistogramConfigInterface, HistogramEvents }
