import { timeFormat } from 'd3-time-format'
import { format as d3format } from 'd3-format'
import { timeSecond, timeMinute, timeHour, timeDay, timeMonth, timeWeek, timeYear } from 'd3-time'

const formatMillisecond = timeFormat('.%L')
const formatSecond = timeFormat(':%S')
const formatMinute = timeFormat('%I:%M')
const formatHour = timeFormat('%I %p')
const formatDay = timeFormat('%a %d')
const formatWeek = timeFormat('%b %d')
const formatMonth = timeFormat('%b')
const formatYear = timeFormat('%Y')


export const defaultDateFormat = (date: Date | number): string => {
  const _date = new Date(date)
  return (timeSecond(_date) < _date
    ? formatMillisecond
    : timeMinute(_date) < _date
      ? formatSecond
      : timeHour(_date) < _date
        ? formatMinute
        : timeDay(_date) < _date
          ? formatHour
          : timeMonth(_date) < _date
            ? (timeWeek(_date) < _date ? formatDay : formatWeek)
            : timeYear(_date) < _date
              ? formatMonth
              : formatYear)(_date)
}

export const defaultNumFormat = (num: number): string => {
  const f = d3format('.5~g')
  return f(num)
}
