import { Graph, GraphConfigInterface, GraphEvents, GraphSimulationSettings, CosmosInputNode, CosmosInputLink } from '@cosmograph/cosmos'
import { CssLabel, LabelRenderer, LabelOptions } from '@interacta/css-labels'
import { merge } from '@cosmograph/ui'
import { color } from 'd3-color'
import { watermark } from './watermark'
import { addMetrics } from '../../api/supabase/supabase'
import s from './style.module.css'

import { defaultCosmographConfig, type CosmographConfigInterface, type CosmographInputConfig } from './config'
import type { CosmographData } from './types'
import { Crossfilter, Filter } from './crossfilter'

export class Cosmograph<N extends CosmosInputNode, L extends CosmosInputLink> {
  private _data: CosmographData<N, L> = { nodes: [], links: [] }
  private _previousData: CosmographData<N, L> = { nodes: [], links: [] }
  private _config: CosmographConfigInterface<N, L> = {}
  private _containerNode: HTMLDivElement
  private _labelsDivElement: HTMLDivElement
  private _watermarkDivElement: HTMLDivElement
  private _canvasElement: HTMLCanvasElement
  private _hoveredCssLabel: CssLabel
  private _cssLabelsRenderer: LabelRenderer
  private _selectedNodesSet: Set<N> | undefined
  private _nodesForTopLabels = new Set<N>()
  private _nodesForForcedLabels = new Set<N>()
  private _trackedNodeToLabel = new Map<N, string>()
  private _isLabelsDestroyed = false
  private _svgParser = new DOMParser()
  private _nodesCrossfilter = new Crossfilter<N>(this._applyLinksFilter.bind(this))
  private _linksCrossfilter = new Crossfilter<L>(this._applyNodesFilter.bind(this))
  /** Filters nodes based on a links crossfilter result  */
  private _nodesFilter = this._nodesCrossfilter.addFilter(false)
  /** Filters links based on a nodes crossfilter result  */
  private _linksFilter = this._linksCrossfilter.addFilter(false)
  /** Filters node based on selected nodes */
  private _selectedNodesFilter = this._nodesCrossfilter.addFilter()

  public cosmos: Graph<N, L>

  public constructor (containerNode: HTMLDivElement, config?: CosmographInputConfig<N, L>) {
    this._containerNode = containerNode
    this._containerNode.classList.add(s.cosmograph)
    this._config = merge(defaultCosmographConfig, config ?? {})

    this._canvasElement = document.createElement('canvas')

    this._labelsDivElement = document.createElement('div')
    this._watermarkDivElement = document.createElement('div')
    this._watermarkDivElement.classList.add(s.watermark)
    this._watermarkDivElement.onclick = () => window.open('https://cosmograph.app/', '_blank')?.focus()

    containerNode.appendChild(this._canvasElement)
    containerNode.appendChild(this._labelsDivElement)
    containerNode.appendChild(this._watermarkDivElement)

    this.cosmos = new Graph(this._canvasElement, this._createCosmosConfig(config))

    this._linksFilter.setAccessor(d => ([d.source, d.target]))
    this._nodesFilter.setAccessor(d => d.id)
    this._selectedNodesFilter.setAccessor(d => d.id)
    this._nodesCrossfilter.onFiltered = () => {
      let selectedNodes
      if (this._nodesCrossfilter.isAnyFiltersActive()) {
        selectedNodes = this._nodesCrossfilter.getFilteredRecords()
        this.cosmos.selectNodesByIds(selectedNodes.map(d => d.id))
      } else {
        this.cosmos.unselectNodes()
      }
      this._updateSelectedNodesSet(selectedNodes)
      this._config.onNodesFiltered?.(selectedNodes)
    }
    this._linksCrossfilter.onFiltered = () => {
      let selectedLinks
      if (this._linksCrossfilter.isAnyFiltersActive()) {
        selectedLinks = this._linksCrossfilter.getFilteredRecords()
      }
      this._config.onLinksFiltered?.(selectedLinks)
    }

    const svgElement = this._svgParser.parseFromString(watermark, 'image/svg+xml').firstChild as SVGElement
    this._watermarkDivElement?.appendChild(svgElement)

    this._cssLabelsRenderer = new LabelRenderer(this._labelsDivElement)
    this._hoveredCssLabel = new CssLabel(this._labelsDivElement)
  }

  public get data (): CosmographData<N, L> {
    return this._data
  }

  /**
   * Sets the data for the graph.
   * @param nodes - Nodes to be added to the graph.
   * @param links - Links to be added to the graph.
   */
  public setData (nodes: N[], links: L[]): void {
    this._data = { nodes, links }
    this.cosmos.setData(nodes, links)
    this._nodesCrossfilter.addRecords(nodes)
    this._linksCrossfilter.addRecords(links)
    this._updateLabels()
    this._config.onSetData?.(nodes, links)

    if (this._isDataDifferent()) {
    /* eslint-disable @typescript-eslint/naming-convention */
      if (!['cosmograph.app'].includes(window.location.hostname)) {
        addMetrics({
          browser: navigator.userAgent,
          hostname: window.location.hostname,
          mode: null,
          is_library_metric: true,
          links_count: links.length,
          links_have_time: null,
          links_raw_columns: Object.keys(links?.[0] as L).length ?? 0,
          links_raw_lines: null,
          nodes_count: nodes.length,
          nodes_have_time: null,
          nodes_raw_columns: Object.keys(nodes?.[0] as N).length ?? 0,
          nodes_raw_lines: null,
        })
      }
    }
    this._previousData = { nodes, links }
    /* eslint-enable @typescript-eslint/naming-convention */
  }

  /**
   * Sets the config for the graph.
   * @param config - Config to be applied to the graph.
   */
  public setConfig (config?: CosmographInputConfig<N, L>): void {
    this._config = merge(defaultCosmographConfig, config ?? {})
    this.cosmos.setConfig(this._createCosmosConfig(config))
    if (config?.backgroundColor) {
      const hexColor = color(config?.backgroundColor)?.formatHex()
      if (hexColor) {
        const brightness = this._checkBrightness(hexColor)
        const root = document.querySelector(':root') as HTMLDivElement
        if (brightness > 0.65) {
          root?.style.setProperty('--cosmograph-watermark-color', '#000000')
        } else {
          root?.style.setProperty('--cosmograph-watermark-color', '#ffffff')
        }
      }
    }
    this._updateLabels()
  }

  /**
   * Creates a filter for the nodes, adds the filter to the nodes crossfilter and returns this filter.
   */
  public addNodesFilter (): Filter<N> {
    return this._nodesCrossfilter.addFilter()
  }

  /**
   * Creates a filter for the links, adds the filter to the links crossfilter and returns this filter.
   */
  public addLinksFilter (): Filter<L> {
    return this._linksCrossfilter.addFilter()
  }

  /**
   * Selects nodes inside a rectangular area.
   * @param selection Array of two corners of the rectangle `[[left, top], [right, bottom]]`.
   * The `left` and `right` coordinates should be relative to the width of the canvas. The
   * `top` and `bottom` coordinates should be relative to the height of the canvas.
   */
  public selectNodesInRange (selection: [[number, number], [number, number]] | null): void {
    this.cosmos.selectNodesInRange(selection)
    const selectedNodeIds = new Set((this.cosmos.getSelectedNodes() ?? []).map(d => d.id))
    this._selectedNodesFilter.applyFilter(d => selectedNodeIds.has(d as string))
  }

  /**
   * Selects nodes.
   * @param nodes Array of nodes to be selected.
   */
  public selectNodes (nodes: N[]): void {
    const selectedNodeIds = new Set(nodes.map(d => d.id))
    this._selectedNodesFilter.applyFilter(d => selectedNodeIds.has(d as string))
  }

  /**
   * Selects a node and, optionally, select its connected nodes.
   * @param node Selected node.
   * @param selectAdjacentNodes Optional parameter determining whether to also select the connected nodes.
   */
  public selectNode (node: N, selectAdjacentNodes = false): void {
    const selectedNodes = new Set([node, ...(selectAdjacentNodes ? (this.cosmos.getAdjacentNodes(node.id) || []) : [])].map(d => d.id))
    this._selectedNodesFilter.applyFilter(d => selectedNodes.has(d as string))
  }

  /**
   * Unselects all nodes.
   */
  public unselectNodes (): void {
    this._selectedNodesFilter.clear()
  }

  /**
   * Center the view and zoom in to a node.
   * @param node Node to be zoomed in.
   */
  public zoomToNode (node: N): void {
    this.cosmos.zoomToNodeById(node.id)
  }

  /**
   * Set focus on a node. A ring will be drawn around the focused node.
   * If no node is focused, the ring will be cleared.
   * @param node Node to be focused.
   */
  public focusNode (node?: N): void {
    this.cosmos.setFocusedNodeById(node?.id)
  }

  /**
   * Starts the simulation.
   * @param alpha Value between 0 and 1. The higher the value,
   * the more initial energy the simulation will get.
   */
  public start (alpha = 1): void {
    this.cosmos.start(alpha)
  }

  /**
   * Pause the simulation.
   */
  public pause (): void {
    this.cosmos.pause()
  }

  /**
   * Restarts the simulation.
   */
  public restart (): void {
    this.cosmos.restart()
  }

  /**
   * Destroy the graph and clean up the context.
   */
  public remove (): void {
    this.cosmos.destroy()
    if (this._isLabelsDestroyed) return
    this._containerNode.innerHTML = ''
    this._isLabelsDestroyed = true
    this._hoveredCssLabel.destroy()
    this._cssLabelsRenderer.destroy()
  }

  private _createCosmosConfig (config?: CosmographInputConfig<N, L>): GraphConfigInterface<N, L> {
    return {
      ...config,
      simulation: {
        ...Object.keys(config ?? {})
          .filter(d => d.indexOf('simulation') !== -1)
          .reduce<Record<string, any>>((acc, d) => {
          const key = d.replace('simulation', '')
          acc[key.charAt(0).toLowerCase() + key.slice(1)] = config?.[d as keyof CosmographInputConfig<N, L>]
          return acc
        }, {}),
        onStart: this._onStart.bind(this),
        onTick: this._onTick.bind(this),
        onEnd: this._onEnd.bind(this),
        onPause: this._onPause.bind(this),
        onRestart: this._onRestart.bind(this),
      },
      events: {
        onClick: this._onClick.bind(this),
        onNodeMouseOver: this._onNodeMouseOver.bind(this),
        onNodeMouseOut: this._onNodeMouseOut.bind(this),
        onMouseMove: this._onMouseMove.bind(this),
        onZoomStart: this._onZoomStart.bind(this),
        onZoom: this._onZoom.bind(this),
        onZoomEnd: this._onZoomEnd.bind(this),
      },
    }
  }

  private _updateLabels (): void {
    if (this._isLabelsDestroyed) return
    const { data: { nodes }, _config: { showTopLabels, showTopLabelsLimit, showLabelsFor, showTopLabelsValueKey, nodeLabelAccessor } } = this
    this._nodesForTopLabels.clear()

    if (showTopLabels && showTopLabelsLimit) {
      let sortedNodes: (N | undefined)[]
      if (showTopLabelsValueKey) {
        sortedNodes = [...nodes].sort((a, b) => {
          const aValue = a[showTopLabelsValueKey]
          const bValue = b[showTopLabelsValueKey]

          if (typeof aValue === 'number' && typeof bValue === 'number') return bValue - aValue
          else return 0
        })
      } else {
        sortedNodes = Object.entries(this.cosmos.graph.degree)
          .sort((a, b) => b[1] - a[1])
          .slice(0, showTopLabelsLimit)
          .map(d => this.cosmos.graph.getNodeByIndex(+d[0]))
      }
      for (let i = 0; i < showTopLabelsLimit; i++) {
        if (i >= nodes.length) break
        const node = sortedNodes[i]
        if (node) this._nodesForTopLabels.add(node)
      }
    }
    this._nodesForForcedLabels.clear()
    showLabelsFor?.forEach(this._nodesForForcedLabels.add, this._nodesForForcedLabels)

    this._trackedNodeToLabel.clear()
    this.cosmos.trackNodePositionsByIds([
      ...(showTopLabels ? this._nodesForTopLabels : []),
      ...this._nodesForForcedLabels,
    ].map(d => {
      this._trackedNodeToLabel.set(d, nodeLabelAccessor?.(d) ?? d.id)
      return d.id
    }))
    this._renderLabels()
  }

  private _updateSelectedNodesSet (selectedNodes: N[] | undefined): void {
    if (this._isLabelsDestroyed) return
    if (selectedNodes) {
      this._selectedNodesSet = new Set<N>()
      selectedNodes?.forEach(this._selectedNodesSet.add, this._selectedNodesSet)
    } else {
      this._selectedNodesSet = undefined
    }
    this._renderLabels()
  }

  private _renderLabels (): void {
    if (this._isLabelsDestroyed) return
    const { _selectedNodesSet, _config: { showDynamicLabels, nodeLabelAccessor, nodeLabelColor, nodeLabelClassName } } = this
    let labels: LabelOptions[] = []
    const trackedNodesPositions = this.cosmos.getTrackedNodePositionsMap()
    const nodeToLabelInfo = new Map<N, [string | undefined, [number, number] | undefined, string, number]>()
    if (showDynamicLabels) {
      const sampledNodesPositions = this.cosmos.getSampledNodePositionsMap()
      sampledNodesPositions.forEach((positions, id) => {
        const node = this.cosmos.graph.getNodeById(id)
        if (node) nodeToLabelInfo.set(node, [nodeLabelAccessor?.(node) ?? node.id, positions, s.cosmographShowDynamicLabels, 0.7])
      })
    }
    this._nodesForTopLabels.forEach(node => {
      nodeToLabelInfo.set(node, [this._trackedNodeToLabel.get(node), trackedNodesPositions.get(node.id), s.cosmographShowTopLabels, 0.9])
    })
    this._nodesForForcedLabels.forEach(node => {
      nodeToLabelInfo.set(node, [this._trackedNodeToLabel.get(node), trackedNodesPositions.get(node.id), s.cosmographShowLabelsFor, 1.0])
    })
    labels = [...nodeToLabelInfo.entries()].map(([p, [text, positions, className, weight]]) => {
      const screenPosition = this.cosmos.spaceToScreenPosition([positions?.[0] ?? 0, positions?.[1] ?? 0])
      const radius = this.cosmos.spaceToScreenRadius(this.cosmos.config.nodeSizeScale * (this.cosmos.getNodeRadiusById(p.id) as number))
      const hasSelectedNodes = !!_selectedNodesSet
      const isNodeSelected = _selectedNodesSet?.has(p)
      return {
        id: p.id,
        text: text ?? '',
        x: screenPosition[0],
        y: screenPosition[1] - (radius + 2),
        weight: (hasSelectedNodes && !isNodeSelected) ? 0.1 : weight,
        shouldBeShown: this._nodesForForcedLabels.has(p),
        style: (hasSelectedNodes && !isNodeSelected) ? 'opacity: 0.1;' : '',
        color: nodeLabelColor && (typeof nodeLabelColor === 'string' ? nodeLabelColor : nodeLabelColor?.(p)),
        className: (typeof nodeLabelClassName === 'string' ? nodeLabelClassName : nodeLabelClassName?.(p)) ?? className,
      }
    })
    this._cssLabelsRenderer.setLabels(labels)
    this._cssLabelsRenderer.draw(true)
  }

  private _renderLabelForHovered (node?: N, nodeSpacePosition?: [number, number]): void {
    const { _config: { showHoveredNodeLabel, nodeLabelAccessor, hoveredNodeLabelClassName, hoveredNodeLabelColor } } = this
    if (this._isLabelsDestroyed) return

    if (showHoveredNodeLabel && node && nodeSpacePosition) {
      const screenPosition = this.cosmos.spaceToScreenPosition(nodeSpacePosition)
      const radius = this.cosmos.spaceToScreenRadius(this.cosmos.getNodeRadiusById(node.id) as number)
      this._hoveredCssLabel.setText(nodeLabelAccessor?.(node) ?? node.id)
      this._hoveredCssLabel.setVisibility(true)
      this._hoveredCssLabel.setPosition(screenPosition[0], screenPosition[1] - (radius + 2))
      this._hoveredCssLabel.setClassName(
        typeof hoveredNodeLabelClassName === 'string' ? hoveredNodeLabelClassName : hoveredNodeLabelClassName?.(node) ?? ''
      )
      this._hoveredCssLabel.setColor(hoveredNodeLabelColor && (typeof hoveredNodeLabelColor === 'string' ? hoveredNodeLabelColor : hoveredNodeLabelColor?.(node)))
    } else {
      this._hoveredCssLabel.setVisibility(false)
    }
    this._hoveredCssLabel.draw()
  }

  /** Apply crossfiltered nodes result to links crossfilter */
  private _applyLinksFilter (): void {
    if (this._nodesCrossfilter.isAnyFiltersActive(this._nodesFilter)) {
      const crossSelectedNodes = this._nodesCrossfilter.getFilteredRecords(this._nodesFilter)
      const crossSelectedNodeIdSet = new Set(crossSelectedNodes.map(d => d.id))
      this._linksFilter.applyFilter(d => {
        const source = (d as [string, string])?.[0]
        const target = (d as [string, string])?.[1]
        return crossSelectedNodeIdSet.has(source) && crossSelectedNodeIdSet.has(target)
      })
    } else {
      this._linksFilter.clear()
    }
  }

  /** Apply crossfiltered links result to nodes crossfilter */
  private _applyNodesFilter (): void {
    if (this._linksCrossfilter.isAnyFiltersActive(this._linksFilter)) {
      const crossSelectedLinks = this._linksCrossfilter.getFilteredRecords(this._linksFilter)
      const crossSelectedNodeIdSet = new Set(crossSelectedLinks.map(d => ([d.source, d.target])).flat())
      this._nodesFilter.applyFilter(d => crossSelectedNodeIdSet.has(d as string))
    } else {
      this._nodesFilter.clear()
    }
  }

  private _checkBrightness (hex: string): number {
    const hexToRgb = (hex: string): { r: number; g: number; b: number } => {
      const parsed = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex)
      return parsed ? {
        r: parseInt((parsed[1] || 0).toString(), 16),
        g: parseInt((parsed[2] || 0).toString(), 16),
        b: parseInt((parsed[3] || 0).toString(), 16),
      } : { r: 0, g: 0, b: 0 }
    }
    const rgb = hexToRgb(hex)
    return (0.2126 * rgb.r + 0.7152 * rgb.g + 0.0722 * rgb.b) / 255
  }

  private _isDataDifferent = (): boolean => {
    const nodesJSON = JSON.stringify(this._data.nodes)
    const prevNodesJSON = JSON.stringify(this._previousData.nodes)
    const linksJSON = JSON.stringify(this._data.links)
    const prevLinksJSON = JSON.stringify(this._previousData.links)

    if (nodesJSON !== prevNodesJSON || linksJSON !== prevLinksJSON) {
      return true
    } else {
      return false
    }
  }

  private _onClick: Exclude<GraphEvents<N>['onClick'], undefined> = (...args) => {
    this._config.onClick?.(...args)
  }

  private _onNodeMouseOver: Exclude<GraphEvents<N>['onNodeMouseOver'], undefined> = (...args) => {
    this._config.onNodeMouseOver?.(...args)
    const [hoveredNode, , nodePosition] = args
    this._renderLabelForHovered(hoveredNode, nodePosition)
  }

  private _onNodeMouseOut: Exclude<GraphEvents<N>['onNodeMouseOut'], undefined> = (...args) => {
    this._config.onNodeMouseOut?.(...args)
    this._renderLabelForHovered()
  }

  private _onMouseMove: Exclude<GraphEvents<N>['onMouseMove'], undefined> = (...args) => {
    this._config.onMouseMove?.(...args)
    const [node, , nodePosition] = args
    this._renderLabelForHovered(node, nodePosition)
  }

  private _onZoomStart: Exclude<GraphEvents<N>['onZoomStart'], undefined> = (...args) => {
    this._config.onZoomStart?.(...args)
  }

  private _onZoom: Exclude<GraphEvents<N>['onZoom'], undefined> = (...args) => {
    this._config.onZoom?.(...args)
    this._renderLabelForHovered()
    this._renderLabels()
  }

  private _onZoomEnd: Exclude<GraphEvents<N>['onZoomEnd'], undefined> = (...args) => {
    this._config.onZoomEnd?.(...args)
  }

  private _onStart: Exclude<GraphSimulationSettings<N>['onStart'], undefined> = (...args) => {
    this._config.onSimulationStart?.(...args)
  }

  private _onTick: Exclude<GraphSimulationSettings<N>['onTick'], undefined> = (...args) => {
    this._config.onSimulationTick?.(...args)
    this._renderLabels()
  }

  private _onEnd: Exclude<GraphSimulationSettings<N>['onEnd'], undefined> = (...args) => {
    this._config.onSimulationEnd?.(...args)
  }

  private _onPause: Exclude<GraphSimulationSettings<N>['onPause'], undefined> = (...args) => {
    this._config.onSimulationPause?.(...args)
  }

  private _onRestart: Exclude<GraphSimulationSettings<N>['onRestart'], undefined> = (...args) => {
    this._config.onSimulationRestart?.(...args)
  }
}

export type { CosmographData, CosmographConfigInterface, CosmographInputConfig, Filter }
