import React, { Ref, ForwardedRef, useRef, useState, useEffect, useImperativeHandle } from 'react'
import { CosmographSearch as _CosmographSearch, CosmographSearchInputConfig } from '@cosmograph/cosmograph'
import { CosmosInputLink, CosmosInputNode } from '@cosmograph/cosmos'
import { useCosmograph } from '../../index'

export type CosmographSearchProps<N extends CosmosInputNode> = {
  style?: object;
  className?: string;
} & CosmographSearchInputConfig<N>

export type CosmographSearchRef<N extends CosmosInputNode = CosmosInputNode, L extends CosmosInputLink = CosmosInputLink> = _CosmographSearch<N, L> | undefined;

export function CosmographSearchFC<N extends CosmosInputNode, L extends CosmosInputLink> (
  { style, className, ...config }: CosmographSearchProps<N>,
  ref: ForwardedRef<CosmographSearchRef<N, L>>
): React.ReactElement {
  const context = useCosmograph<N, L>()
  const container = useRef<HTMLDivElement>(null)
  const searchInstance = useRef<_CosmographSearch<N, L>>()
  const [isComponentMounted, setComponentMounted] = useState<boolean>(false)

  useEffect(() => {
    if (container.current && context?.cosmograph) {
      searchInstance.current = new _CosmographSearch<N, L>(context?.cosmograph, container.current, config)
      setComponentMounted(true)
    }
    return () => {
      searchInstance.current?.remove()
    }
  }, [context?.cosmograph])

  useEffect(() => {
    searchInstance.current?.setConfig(config)
  }, [config])

  useImperativeHandle(ref, () => searchInstance.current, [isComponentMounted])

  return <div id="CosmographSearch" style={style} className={className} ref={container}/>
}

// https://stackoverflow.com/a/58473012
export const CosmographSearch = React.forwardRef(CosmographSearchFC) as
  <N extends CosmosInputNode, L extends CosmosInputLink>(
    props: CosmographSearchProps<N> & { ref?: Ref<CosmographSearchRef<N, L>> }
  ) => React.ReactElement

export type { CosmographSearchInputConfig }
