import type { SvelteComponent } from 'svelte'
import type { AccessorOption, SearchData } from './types'
import { type SearchConfigInterface, type SearchEvents, defaultSearchConfig } from './config'
import { Events } from './types'

import SearchComponent from './search.svelte'
import { merge } from '../../utils'

export class Search<T extends SearchData> {
  private _search: SvelteComponent
  private _containerNode: HTMLElement
  private _config: SearchConfigInterface<T> = {}

  public constructor (containerNode: HTMLElement, config?: SearchConfigInterface<T>) {
    this._containerNode = containerNode
    this._config = merge(defaultSearchConfig, config ?? {})
    this._search = new SearchComponent({ target: containerNode, props: { config: this._config } })
    this._search.$on(Events.Input, ({ detail }) => this._config.events?.onSearch?.(detail))
    this._search.$on(Events.Select, ({ detail }) => this._config.events?.onSelect?.(detail))
    this._search.$on(Events.Enter, ({ detail }) => this._config.events?.onEnter?.(detail))
    this._search.$on(Events.AccessorSelect, ({ detail }) => this._config.events?.onAccessorSelect?.(detail))
  }

  public setData (data: T[]): void {
    this._search.$set({ data, textInput: '' })
  }

  public setConfig (config?: SearchConfigInterface<T>): void {
    this._config = merge(defaultSearchConfig, config ?? {})
    this._search.$set({ config: this._config, textInput: '' })
  }

  public setListState (state: boolean): void {
    this._search.setListState(state)
  }

  public clearInput (): void {
    this._search.$set({ textInput: '' })
  }

  /**  `getConfig`: Returns current `Search` configuration */
  public getConfig (): SearchConfigInterface<T> {
    return this._config
  }

  public destroy (): void {
    this._containerNode.innerHTML = ''
  }
}

export type { SearchData, SearchConfigInterface, SearchEvents, AccessorOption as SearchAccessorOption }
