import React, { createContext, useContext, ReactElement, useState } from 'react'
import { Cosmograph } from '@cosmograph/cosmograph'
import { CosmosInputLink, CosmosInputNode } from '@cosmograph/cosmos'

export interface CosmographProviderProps<N extends CosmosInputNode, L extends CosmosInputLink> {
  nodes?: N[];
  links?: L[];
}

export type CosmographContextData<N extends CosmosInputNode, L extends CosmosInputLink> = {
  cosmograph?: Cosmograph<N, L>;
  nodes?: N[];
  links?: L[];
  initCosmograph: (cosmograph: Cosmograph<N, L>) => void;
};

export const CosmographContext = createContext<CosmographContextData<any, any> | null>(null)

export function CosmographProvider<N extends CosmosInputNode, L extends CosmosInputLink> (
  { children, nodes, links }: React.PropsWithChildren<CosmographProviderProps<N, L>>
): ReactElement {
  const [cosmograph, setCosmograph] = useState<Cosmograph<N, L>>()

  const initCosmograph = (instance: Cosmograph<N, L>): void => {
    setCosmograph(instance)
  }

  return (
  // TODO: check generic type here
    <CosmographContext.Provider value={{ cosmograph, nodes, links, initCosmograph } as CosmographContextData<N, L> }>
      {children}
    </CosmographContext.Provider>
  )
}

export function useCosmograph<N extends CosmosInputNode, L extends CosmosInputLink> (): CosmographContextData<N, L> | null {
  const context = useContext(CosmographContext as React.Context<CosmographContextData<N, L>>)
  if (!context) {
    // todo
    console.warn('useCosmograph must be used under CosmographProvider')
    // throw new Error('useCosmograph must be used under CosmographProvider')
  }
  return context
}
