import 'd3-transition'
import { ResizeObserver } from '@juggle/resize-observer'
import { axisBottom } from 'd3-axis'
import { brushX, type BrushBehavior, type BrushSelection } from 'd3-brush'
import { scaleTime, scaleSymlog, scaleLinear, type ScaleLinear, type ScaleTime } from 'd3-scale'
import { select, type Selection } from 'd3-selection'
import { extent, rollup, range, pairs } from 'd3-array'
import { play, pause } from './assets'
import { TimelineConfig, type TimelineEvents, type TimelineConfigInterface } from './config'

import type { BarData, TimelineData } from './types'

import s from './style.module.css'
import { getCountsInRange, getInnerDimensions } from '../../utils'

export class Timeline {
  private _animationInterval: ReturnType<typeof setTimeout> | undefined
  private _isAnimationRunning = false
  private _svgParser = new DOMParser()

  private _svg: SVGElement
  private _animationControlDiv: HTMLElement
  private _noDataDiv: HTMLElement
  private _playButtonSvg: SVGElement | undefined
  private _pauseButtonSvg: SVGElement | undefined
  private _containerNode: HTMLElement
  private _resizeObserver: ResizeObserver
  private _axisGroup: Selection<SVGGElement, unknown, null, undefined>
  private _barsGroup: Selection<SVGGElement, unknown, null, undefined>
  private _brushGroup: Selection<SVGGElement, unknown, null, undefined>

  private _height = 0
  private _width = 0
  private _timelineWidth = 0
  private _timelineHeight = 0

  private _config = new TimelineConfig()

  private _barWidth = 0
  private _maxCount = 0

  private _barsData: BarData[] = []
  private _timeData: TimelineData = undefined
  private _dateExtent: [Date, Date] | [number, number] | undefined
  private _bandIntervals: (number | Date)[] = []
  private _currentSelection: [Date, Date] | [number, number] | undefined
  private _currentSelectionInPixels: [number, number] | undefined
  private _isNumericTimeline = false
  private _firstRender = true

  private _yScale = scaleSymlog<number, number, never>()
  private _timeScale = scaleTime<number, number, never>()
  private _numScale = scaleLinear<number, number, never>()
  private _activeAxisScale: ScaleTime<number, number, never> | ScaleLinear<number, number, never> = this._timeScale

  private _timeAxis = axisBottom<Date>(this._timeScale)
  private _numAxis = axisBottom<number>(this._numScale)

  private _brushInstance: BrushBehavior<unknown> | undefined

  public constructor (containerNode: HTMLElement, config?: TimelineConfigInterface) {
    if (config) this._config.init(config)
    this._containerNode = containerNode

    this._svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg')
    this._svg.classList.add(s.timelineSvg)
    this._animationControlDiv = document.createElement('div')
    this._animationControlDiv.classList.add(s.animationControl)

    this._containerNode.classList.add(s.timeline)
    this._containerNode.appendChild(this._svg)

    this._noDataDiv = document.createElement('div')
    select(this._noDataDiv)
      .style('display', 'none')
      .attr('class', s.noData)
      .append('div')
      .text('No timeline data')
    this._containerNode.appendChild(this._noDataDiv)

    if (this._config?.showAnimationControls) {
      const elementExist = setInterval(() => {
        if (this._containerNode !== null) {
          this._initAnimationControls()
          clearInterval(elementExist)
        }
      }, 100)
    }

    this._barsGroup = select(this._svg).append('g').attr('class', s.bars)
    this._axisGroup = select(this._svg).append('g').attr('class', s.axis)
    this._brushGroup = select(this._svg).append('g').attr('class', s.brush)

    this._timeAxis.tickFormat(this._config.formatter)
    this._numAxis.tickFormat(this._config.formatter)

    this._resizeObserver = new ResizeObserver((entries) => {
      // We wrap it in requestAnimationFrame to avoid this error - ResizeObserver loop limit exceeded https://stackoverflow.com/a/58701523
      window.requestAnimationFrame(() => {
        if (!Array.isArray(entries) || !entries.length) {
          return
        }
        this.resize()
      })
    })
    this._resizeObserver = new ResizeObserver(() => { this.resize() })
    this._resizeObserver.observe(this._containerNode)
  }

  private get _barPadding (): number {
    return this._barWidth * this._config.barPadding
  }

  /**  `getCurrentSelection`: Returns current brush selection in data units (`Date` or `number`). */
  public getCurrentSelection (): [Date, Date] | [number, number] | undefined {
    return this._currentSelection
  }

  /**  `getCurrentSelectionInPixels`: Returns current brush selection in pixels. */
  public getCurrentSelectionInPixels (): [number, number] | undefined {
    return this._currentSelectionInPixels
  }

  /**  `getBarWidth`: Returns computed bar width in pixels */
  public getBarWidth (): number {
    return this._barWidth - this._barPadding
  }

  /**  `getConfig`: Returns current `Timeline` configuration */
  public getConfig (): TimelineConfig {
    return this._config
  }

  /**  `getIsAnimationRunning`: Returns a boolean value indicating if the animation is running. */
  public getIsAnimationRunning (): boolean {
    return this._isAnimationRunning
  }

  /**  `setConfig`: Function for setting config of `Timeline`. */
  public setConfig (config?: TimelineConfigInterface): void {
    const prevConfig = JSON.parse(JSON.stringify(this._config))
    if (!config) {
      this._config = new TimelineConfig()
    } else {
      this._config.init(config)
    }
    if (this._config?.showAnimationControls) {
      if (!this._animationControlDiv?.isConnected) this._initAnimationControls()
    } else if (this._animationControlDiv) {
      this._disableAnimation()
    }
    if (!this._config.allowSelection) {
      this._disableBrush()
    }
    if (this._config.formatter) {
      this._timeAxis.tickFormat(this._config.formatter)
      this._numAxis.tickFormat(this._config.formatter)
    }
    // Data-related props
    if (this._config?.dataStep !== prevConfig.config?.dataStep || this._config?.barCount !== prevConfig.config?.barCount) {
      this._updateTimelineData()
    }
    this.resize()
  }

  /**  `setTimeData`: Function for setting data of `Timeline`. */
  public setTimeData (timeData: TimelineData): void {
    this._timeData = timeData?.filter(d => !isNaN(+d) && d !== undefined)
    this._currentSelection = undefined
    this._config.events.onBrush?.(this._currentSelection)
    this._updateScales()
    select(this._noDataDiv).style('display', 'none')
    if (this._timeData?.length) {
      this._dateExtent = extent(this._timeData) as [number, number] | [Date, Date]
      this._updateTimelineData()
    } else {
      this._barsData = []
      this._axisGroup.selectAll('*').remove()
      this._barsGroup.selectAll('*').remove()
      this._brushGroup.selectAll('*').remove()
      select(this._noDataDiv).style('display', 'block')
      this._firstRender = true
    }
  }

  private _getBarsData (rangeForIntervals: [number, number], date?: boolean): void {
    if (rangeForIntervals[1] <= rangeForIntervals[0]) return
    if (this._timeData?.length && this._dateExtent) {
      const countedDates = rollup(this._timeData, v => v.length, d => d)
      const step = this._config.dataStep ?? (rangeForIntervals[1] - rangeForIntervals[0]) / (this._config.barCount - 1)
      if (step === 0) return
      this._bandIntervals = range(+rangeForIntervals[0], +rangeForIntervals[1], step)
      const lastTick = this._bandIntervals[this._bandIntervals.length - 1] as number
      let lastInterval: Date | number = this._config.dataStep ? +lastTick + step : rangeForIntervals[1]
      if (date) {
        this._bandIntervals = this._bandIntervals.map(d => new Date(d))
        lastInterval = new Date(lastInterval)
      }
      if (lastTick < rangeForIntervals[1]) this._bandIntervals.push(lastInterval)
      const datePairs = pairs(this._bandIntervals)
      this._barsData = datePairs.map(d => ({
        rangeStart: d[0],
        rangeEnd: d[1],
        count: getCountsInRange(countedDates, d),
      }))
    }
  }

  private _updateTimelineData (): void {
    if (this._timeData?.length && this._dateExtent) {
      this._isNumericTimeline = !(this._timeData[0] instanceof Date)
      if (this._isNumericTimeline) {
        this._getBarsData(this._dateExtent as [number, number])
      } else {
        this._timeData = this._timeData.map(d => new Date(d)) as [Date, Date]
        const extent = (this._dateExtent as [Date, Date]).map(d => d.getTime() ?? 0)
        this._getBarsData(extent as [number, number])
      }
      this._maxCount = Math.max(...this._barsData.map(d => d.count))
    }
  }

  /**  `setSelection`: Set the selected range on a `Timeline`. Takes a selection range as a parameter, which can be a range of dates or a range of numbers if `TimelineData` is numeric. */
  public setSelection (selectionRange?: [Date, Date] | [number, number], renderOnly = false): void {
    const prevSelection = this._currentSelection
    if (selectionRange && this._dateExtent && selectionRange[0] >= this._dateExtent[0] && selectionRange[1] <= this._dateExtent[1] && selectionRange[0] < selectionRange[1]) {
      this._currentSelection = selectionRange
      this._currentSelectionInPixels = this._currentSelection.map(this._activeAxisScale) as [number, number]
      this._animationControlDiv?.classList.remove(s.disabled)
    } else {
      this._currentSelection = undefined
      this._currentSelectionInPixels = undefined
      this._animationControlDiv?.classList.add(s.disabled)
    }
    const { _currentSelection } = this
    if (!renderOnly && (prevSelection?.[0] !== _currentSelection?.[0] || prevSelection?.[1] !== _currentSelection?.[1])) {
      this._config.events.onBrush?.(this._currentSelection, true)
    }
    if (this._brushInstance && !this._firstRender) {
      this._brushGroup.call(this._brushInstance.move, this._currentSelectionInPixels as BrushSelection)
    }
  }

  /**  `setSelectionInPixels`: Set the selected range on a `Timeline` in pixels. Takes an array containing two numeric values representing selection range in pixels. */
  public setSelectionInPixels (coordinates?: [number, number]): void {
    if (coordinates && coordinates[0] > 0 && coordinates[1] < this._timelineWidth && coordinates[0] < coordinates[1]) {
      this._currentSelection = coordinates.map(d => this._activeAxisScale.invert(d)) as [Date, Date] | [number, number]
      this._currentSelectionInPixels = this._currentSelection?.map(this._activeAxisScale) as [number, number]
      this._animationControlDiv?.classList.remove(s.disabled)
    } else {
      this._currentSelection = undefined
      this._currentSelectionInPixels = undefined
      this._animationControlDiv?.classList.add(s.disabled)
    }
    if (this._brushInstance && !this._firstRender) {
      this._brushGroup.call(this._brushInstance.move, this._currentSelectionInPixels as [number, number])
    }
    this._config.events.onBrush?.(this._currentSelection)
  }

  /**  `resize`: Resizes `Timeline` according to the parent node attributes. */
  public resize (): void {
    const { height, width } = getInnerDimensions(this._containerNode)
    const { offsetWidth: animationWidth } = this._animationControlDiv
    this._width = width
    this._height = height
    this._timelineWidth = this._width - this._config.padding.left - this._config.padding.right - animationWidth
    this._timelineHeight = this._height - this._config.padding.top - this._config.padding.bottom
    if (this._timelineHeight > (this._config.padding.top + this._config.padding.bottom)) {
      this._updateScales()
      this._checkLastTickPosition()
      if (this._currentSelection) this.setSelection(this._currentSelection, true)
      this.render()
    }
  }

  /**  `render`: Renders `Timeline`. */
  public render (): void {
    this._updateBrush()
    this._updateBars()
    this._updateAxis()
    if (this._firstRender) this._firstRender = false
  }

  private _updateAxis (): void {
    if (!this._timeData) return
    this._axisGroup
      .style('transform', `translate(${this._config.padding.left}px, ${this._config.padding.top + this._config.axisTickHeight + 1 + this._config.selectionPadding / 2}px)`)
      .call(this._isNumericTimeline ? this._numAxis : this._timeAxis)
      .call(g => g.select('.domain').remove())

    this._axisGroup.selectAll('.tick')
      .select('text')
      .attr('class', s.axisTick)
      .attr('y', 0)
      .attr('dy', -this._config.axisTickHeight)
      .attr('dx', '5px')

    this._axisGroup.selectAll('line')
      .attr('class', s.axisLine)
      .attr('y2', -this._config.axisTickHeight)
  }

  private _updateBrush (): void {
    if (!this._config.allowSelection) return
    this._brushGroup.style('transform', `translate(${this._config.padding.left}px, ${this._config.padding.top}px)`)
    this._brushInstance = brushX().extent([[0, 0], [this._timelineWidth, this._timelineHeight]])
    this._brushInstance.on('end', ({ selection, sourceEvent }: {selection: number[] | Date[]; sourceEvent: MouseEvent}) => {
      if (!sourceEvent) return
      if (selection) {
        this._currentSelection = selection.map(d => this._activeAxisScale.invert(d)) as [Date, Date] | [number, number]
        this._currentSelectionInPixels = this._currentSelection?.map(this._activeAxisScale) as [number, number]
        this._animationControlDiv?.classList.remove(s.disabled)
        this._config.events.onBrush?.(this._currentSelection)
      } else {
        this._currentSelection = undefined
        this._currentSelectionInPixels = undefined
        this._config.events.onBrush?.(undefined)
        this._animationControlDiv?.classList.add(s.disabled)
      }
    })
    this._brushGroup.call(this._brushInstance)
    if (this._currentSelection) {
      this._currentSelectionInPixels = this._currentSelection.map(this._activeAxisScale) as [number, number]
      this._brushGroup.call(this._brushInstance.move, this._currentSelectionInPixels)
    } else {
      this._brushInstance?.clear(this._brushGroup)
    }
    this._brushGroup.select('rect.selection')
      .classed(s.selection, true)
      .attr('rx', this._config.selectionRadius)
      .attr('ry', this._config.selectionRadius)
  }

  private _updateBars (): void {
    this._barsGroup.style('transform', `translate(${this._config.padding.left}px, ${this._config.padding.top - this._config.selectionPadding / 2}px)`)
    const bars = this._barsGroup.selectAll(`.${s.bar}`)
      .data(this._barsData)
      .join('rect')
      .attr('class', s.bar)
      .attr('x', d => this._activeAxisScale(+d.rangeStart) + this._barPadding / 2)
      .attr('width', this.getBarWidth())
      .attr('rx', this._config.barRadius)
      .attr('ry', this._config.barRadius)
      .attr('y', -this._timelineHeight)

    if (this._config.events.onBarHover) bars.on('mouseover', this._config.events.onBarHover)

    bars.transition().duration(300)
      .attr('height', (d: BarData) => this._yScale(d.count))
      .style('opacity', (d: BarData) => this._yScale(d.count) === this._config.minBarHeight ? 0.25 : 1)
  }

  private _updateScales (): void {
    if (!this._dateExtent || !this._barsData.length) return
    const lastBarData = this._barsData[this._barsData.length - 1] as BarData
    if (this._config.tickStep) {
      const ticks = range(+this._dateExtent[0], +this._dateExtent[1], this._config.tickStep)
      if (this._isNumericTimeline) {
        this._numAxis.tickValues(ticks)
      } else {
        this._timeAxis.tickValues(ticks.map(d => new Date(d)))
      }
    }
    this._yScale
      .range([this._config.minBarHeight, this._timelineHeight - this._config.barTopMargin - this._config.selectionPadding])
      .domain([0, this._maxCount])
      .clamp(true)
    if (this._isNumericTimeline) {
      this._numScale
        .domain([this._dateExtent[0], lastBarData.rangeEnd])
        .range([0, this._timelineWidth])
        .clamp(true)
      this._activeAxisScale = this._numScale
    } else {
      this._timeScale
        .domain([this._dateExtent[0], lastBarData.rangeEnd])
        .range([0, this._timelineWidth])
        .clamp(true)
      this._activeAxisScale = this._timeScale
    }
    const firstBar = this._barsData[0] as BarData
    const diff = this._activeAxisScale(firstBar.rangeEnd) - this._activeAxisScale(firstBar.rangeStart)
    this._barWidth = diff
  }

  private _disableBrush (): void {
    this._brushInstance?.clear(this._brushGroup)
    this._currentSelectionInPixels = undefined
    this._currentSelection = undefined
    this.pauseAnimation()
    this._brushGroup.selectAll('*').remove()
    if (this._config.showAnimationControls) this._animationControlDiv?.classList.add(s.disabled)
  }

  private async _initAnimationControls (): Promise<void> {
    this._containerNode.insertBefore(this._animationControlDiv, this._svg)
    const getIcons = async (): Promise<void> => {
      if (!this._animationControlDiv.firstChild) {
        const playButton = this._svgParser.parseFromString(play, 'image/svg+xml').firstChild as SVGElement
        const pauseButton = this._svgParser.parseFromString(pause, 'image/svg+xml').firstChild as SVGElement
        this._pauseButtonSvg = this._animationControlDiv?.appendChild(pauseButton)
        this._playButtonSvg = this._animationControlDiv?.appendChild(playButton)
      }
    }
    await getIcons().then(() => {
      if (!this._isAnimationRunning) {
        this._playButtonSvg?.classList.add(s.playAnimation)
        this._pauseButtonSvg?.classList.add(s.pauseAnimation, s.hidden)
      } else {
        this._playButtonSvg?.classList.add(s.playAnimation, s.hidden)
        this._pauseButtonSvg?.classList.add(s.pauseAnimation)
      }
      if (!this._currentSelection) this._animationControlDiv?.classList.add(s.disabled)
      this._animationControlDiv.addEventListener('click', this._toggleAnimation)
    })
  }

  private _toggleAnimation = (e: Event): void => {
    e.preventDefault()
    if (!this._isAnimationRunning) this.playAnimation()
    else this.pauseAnimation()
  }

  private _disableAnimation = (): void => {
    this.pauseAnimation()
    this._animationControlDiv?.removeEventListener('click', this._toggleAnimation)
    this._animationControlDiv?.remove()
  }

  /**  `playAnimation`: If some interval is selected on `Timeline`, starts animation for it. The selected interval is moved forward by each timeline bar according to the speed passed in the `animationSpeed` of the `Timeline` `config`. */
  public playAnimation = (): void => {
    clearInterval(this._animationInterval)
    if (this._currentSelectionInPixels) {
      this._animationInterval = setInterval(this._animateSelection, this._config.animationSpeed)
      this._isAnimationRunning = true
      this._config.events.onAnimationPlay?.(this._isAnimationRunning, this._currentSelection)
    }
    this._pauseButtonSvg?.classList.remove(s.hidden)
    this._playButtonSvg?.classList.add(s.hidden)
  }

  /**  `pauseAnimation`: Pauses animation of selected timeline interval. */
  public pauseAnimation = (): void => {
    clearInterval(this._animationInterval)
    this._isAnimationRunning = false
    this._config.events.onAnimationPause?.(this._isAnimationRunning, this._currentSelection)
    this._pauseButtonSvg?.classList.add(s.hidden)
    this._playButtonSvg?.classList.remove(s.hidden)
  }

  /**  `stopAnimation`: Same as `pauseAnimation()`, but resets selection and returns `undefined` value for the `onBrush` callback. */
  public stopAnimation = (): void => {
    this.pauseAnimation()
    this.setSelection(undefined)
    this._config.events.onBrush?.(undefined)
  }

  private _animateSelection = (): void => {
    const currentTimelineSelection = this._currentSelectionInPixels
    if (!currentTimelineSelection) return
    if (currentTimelineSelection[0] !== undefined && currentTimelineSelection[1] !== undefined) {
      this.setSelectionInPixels([currentTimelineSelection[0] + this._barWidth, currentTimelineSelection[1] + this._barWidth])
      if (currentTimelineSelection[1] === this._currentSelectionInPixels?.[1] || this._currentSelectionInPixels?.[1] === undefined) {
        this.stopAnimation()
      }
    }
  }

  private _checkLastTickPosition = (): void => {
    const lastTickSelection = this._axisGroup.selectAll('.tick:last-of-type').nodes()
    if (lastTickSelection?.length) {
      const lastTick = (lastTickSelection[0] as HTMLElement)
      const lastTickRight = lastTick?.getBoundingClientRect().right
      const svgRight = this._svg?.getBoundingClientRect().right
      lastTick.style.display = lastTickRight >= svgRight ? 'none' : 'inherit'
      // todo: runs twice with unusual small lastTickRight
    }
  }

  public destroy = (): void => {
    this._containerNode.innerHTML = ''
    clearInterval(this._animationInterval)
  }
}

export { TimelineConfig }
export type { TimelineData, TimelineConfigInterface, TimelineEvents }
