import crossfilter from 'crossfilter2'

type OnFilterCallback = () => void
type OnDataAddedCallback = () => void
type OnDataRemovedCallback = () => void

export class Filter<Record> {
  private _crossfilter: crossfilter.Crossfilter<Record>
  private _dimension: crossfilter.Dimension<Record, string | number> | undefined
  private _selfRemove: () => void
  private _syncUp: (() => void) | undefined
  public onFiltered: OnFilterCallback | undefined
  public onDataAdded: OnDataAddedCallback | undefined
  public onDataRemoved: OnDataRemovedCallback | undefined

  constructor (crossfilter: crossfilter.Crossfilter<Record>, selfRemove: () => void, syncUp?: () => void) {
    this._crossfilter = crossfilter
    this._selfRemove = selfRemove
    this._syncUp = syncUp
  }

  public setAccessor (selector: crossfilter.OrderedValueSelector<Record, string | string[] | number | number[]>): void {
    const { _crossfilter } = this
    this.dispose()
    this._dimension = _crossfilter.dimension(selector)
  }

  public applyFilter (filterValue: (d: unknown) => boolean): void {
    this._dimension?.filter(filterValue)
    this._syncUp?.()
  }

  public clear (): void {
    this._dimension?.filterAll()
    this._syncUp?.()
  }

  public getAllValues (): crossfilter.NaturallyOrderedValue[] | undefined {
    const { _crossfilter, _dimension } = this
    if (!_dimension) return undefined
    return _crossfilter.all().map(_dimension.accessor)
  }

  public getFilteredValues (): crossfilter.NaturallyOrderedValue[] | undefined {
    const { _crossfilter, _dimension } = this
    if (!_dimension) return undefined
    return _crossfilter.allFiltered().map(_dimension.accessor)
  }

  public getFilteredRecords (): Record[] {
    // Crossfilter declaration file does not have any arguments for `allFiltered` function,
    // but it is present in the code - https://github.com/crossfilter/crossfilter/wiki/API-Reference#crossfilter_isElementFiltered
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    return this._crossfilter.allFiltered([this._dimension])
  }

  public isActive (): boolean {
    return this._dimension?.hasCurrentFilter() || false
  }

  public dispose (): void {
    this._dimension?.dispose()
  }

  public remove (): void {
    this.dispose()
    this._selfRemove()
  }
}

export class Crossfilter<Record> {
  private _crossfilter = crossfilter<Record>()
  private _records: Record[] | undefined
  private _filters = new Set<Filter<Record>>()
  private _syncUpFunction: () => void
  public onFiltered: OnFilterCallback | undefined
  public onDataAdded: OnDataAddedCallback | undefined
  public onDataRemoved: OnDataRemovedCallback | undefined

  constructor (syncUpFunction: () => void) {
    this._syncUpFunction = syncUpFunction
    this._crossfilter.onChange(eventType => {
      switch (eventType) {
        case 'filtered': {
          this.onFiltered?.()
          this._filters.forEach(filter => { filter.onFiltered?.() })
          break
        }
        case 'dataAdded': {
          this.onDataAdded?.()
          this._filters.forEach(filter => { filter.onDataAdded?.() })
          break
        }
        case 'dataRemoved':{
          this.onDataRemoved?.()
          this._filters.forEach(filter => { filter.onDataRemoved?.() })
          break
        }
      }
    })
  }

  public addRecords (records: Record[]): void {
    const { _crossfilter } = this
    this._records = records
    _crossfilter.remove()
    _crossfilter.add(records)
  }

  public getFilteredRecords (ignoreFilter?: Filter<Record>): Record[] {
    const { _crossfilter } = this
    return ignoreFilter?.getFilteredRecords() || _crossfilter.allFiltered()
  }

  public addFilter (runSyncOnApply = true): Filter<Record> {
    const filter = new Filter<Record>(this._crossfilter, () => { this._filters.delete(filter) }, runSyncOnApply ? this._syncUpFunction : undefined)
    this._filters.add(filter)
    return filter
  }

  public clearFilters (): void {
    this._filters.forEach(filter => {
      filter.clear()
    })
  }

  public isAnyFiltersActive (exceptFilter?: Filter<Record>): boolean {
    for (const filter of this._filters.values()) {
      if (filter !== exceptFilter && filter.isActive()) return true
    }
    return false
  }

  public getAllRecords (): Record[] | undefined {
    return this._records
  }
}
