import React, { Ref, ForwardedRef, useRef, useState, useEffect, useImperativeHandle } from 'react'
import { CosmographTimeline as _CosmographTimeline, CosmographTimelineInputConfig } from '@cosmograph/cosmograph'
import { CosmosInputLink, CosmosInputNode } from '@cosmograph/cosmos'
import { useCosmograph } from '../../context'

export type CosmographTimelineProps<Datum> = {
  style?: object;
  className?: string;
} & CosmographTimelineInputConfig<Datum>

export type CosmographTimelineRef<Datum> = _CosmographTimeline<Datum> | undefined;

export function CosmographTimelineFC<Datum> (
  { style, className, ...config }: CosmographTimelineProps<Datum>,
  ref: ForwardedRef<CosmographTimelineRef<Datum>>
): React.ReactElement {
  const context = useCosmograph<CosmosInputNode, CosmosInputLink>()
  const container = useRef<HTMLDivElement>(null)
  const timelineInstance = useRef<_CosmographTimeline<Datum>>()
  const [isComponentMounted, setComponentMounted] = useState<boolean>(false)

  useEffect(() => {
    if (container.current && context?.cosmograph) {
      timelineInstance.current = new _CosmographTimeline(context?.cosmograph, container.current, config)
      setComponentMounted(true)
    }
    return () => {
      timelineInstance.current?.remove()
    }
  }, [context?.cosmograph])

  useEffect(() => {
    timelineInstance.current?.setConfig(config)
  }, [config])

  useImperativeHandle(ref, () => timelineInstance.current, [isComponentMounted])

  return <div id="CosmographTimeline" style={style} className={className} ref={container}/>
}

// https://stackoverflow.com/a/58473012
export const CosmographTimeline = React.forwardRef(CosmographTimelineFC) as
  <Datum>(
    props: CosmographTimelineProps<Datum> & { ref?: Ref<CosmographTimelineRef<Datum>> }
  ) => React.ReactElement

export type { CosmographTimelineInputConfig }
