import React, { Ref, ForwardedRef, useRef, useState, useEffect, useImperativeHandle } from 'react'
import { CosmographHistogram as _CosmographHistogram, CosmographHistogramInputConfig } from '@cosmograph/cosmograph'
import { CosmosInputLink, CosmosInputNode } from '@cosmograph/cosmos'
import { useCosmograph } from '../../context'

export type CosmographHistogramProps<OutDatum, InDatum = OutDatum> = {
  style?: object;
  className?: string;
} & CosmographHistogramInputConfig<OutDatum, InDatum>

export type CosmographHistogramRef<OutDatum, InDatum = OutDatum> = _CosmographHistogram<OutDatum, InDatum> | undefined;

export function CosmographHistogramFC<OutDatum, InDatum = OutDatum> (
  { style, className, ...config }: CosmographHistogramProps<OutDatum, InDatum>,
  ref: ForwardedRef<_CosmographHistogram<OutDatum, InDatum> | undefined>
): React.ReactElement {
  const context = useCosmograph<CosmosInputNode, CosmosInputLink>()
  const container = useRef<HTMLDivElement>(null)
  const histogramInstance = useRef<_CosmographHistogram<OutDatum, InDatum>>()
  const [isComponentMounted, setComponentMounted] = useState<boolean>(false)

  useEffect(() => {
    if (container.current && context?.cosmograph) {
      histogramInstance.current = new _CosmographHistogram(context?.cosmograph, container.current, config)
      setComponentMounted(true)
    }
    return () => {
      histogramInstance.current?.remove()
    }
  }, [context?.cosmograph])

  useEffect(() => {
    histogramInstance.current?.setConfig(config)
  }, [config])

  useImperativeHandle(ref, () => histogramInstance.current, [isComponentMounted])

  return <div id="CosmographHistogram" style={style} className={className} ref={container}/>
}

// https://stackoverflow.com/a/58473012
export const CosmographHistogram = React.forwardRef(CosmographHistogramFC) as
  <OutDatum, InDatum = OutDatum>(
    props: CosmographHistogramProps<OutDatum, InDatum> & { ref?: Ref<_CosmographHistogram<OutDatum, InDatum> | undefined> }
  ) => React.ReactElement

export type { CosmographHistogramInputConfig }
