import React, { Ref, ForwardedRef, useRef, useState, useEffect, useImperativeHandle } from 'react'
import { Cosmograph as _Cosmograph, CosmographInputConfig } from '@cosmograph/cosmograph'
import { CosmosInputLink, CosmosInputNode } from '@cosmograph/cosmos'
import { useCosmograph } from '../../context'

export type CosmographProps<N extends CosmosInputNode, L extends CosmosInputLink> = {
  nodes?: N[];
  links?: L[];
  style?: object;
  className?: string;
} & CosmographInputConfig<N, L>

export type CosmographRef<N extends CosmosInputNode = CosmosInputNode, L extends CosmosInputLink = CosmosInputLink> = _Cosmograph<N, L> | undefined;

export function CosmographFC<N extends CosmosInputNode, L extends CosmosInputLink> (
  { style, className, nodes, links, ...args }: CosmographProps<N, L>,
  ref: ForwardedRef<CosmographRef<N, L>>
): React.ReactElement {
  const context = useCosmograph<N, L>()
  const container = useRef<HTMLDivElement>(null)
  const cosmographInstance = useRef<_Cosmograph<N, L>>()
  const [isComponentMounted, setComponentMounted] = useState<boolean>(false)

  useEffect(() => {
    if (container.current) {
      cosmographInstance.current = new _Cosmograph<N, L>(container.current, args)
      setComponentMounted(true)
      context?.initCosmograph(cosmographInstance.current)
    }

    return () => {
      cosmographInstance.current?.remove()
    }
  }, [])

  useEffect(() => {
    cosmographInstance.current?.setConfig(args)
  }, [args])

  useEffect(() => {
    if (nodes || links) {
      cosmographInstance.current?.setData(nodes ?? [], links ?? [])
    }
  }, [nodes, links])

  useEffect(() => {
    // Nodes and links from the context has a lower priority
    if (!nodes && !links) {
      cosmographInstance.current?.setData(context?.nodes ?? [], context?.links ?? [])
    }
  }, [context?.nodes, context?.links])

  useImperativeHandle(ref, () => cosmographInstance.current, [isComponentMounted])

  return (
    <div style={{ ...style }} className={className} ref={container} />
  )
}

// https://stackoverflow.com/a/58473012
export const Cosmograph = React.forwardRef(CosmographFC) as
  <N extends CosmosInputNode, L extends CosmosInputLink>(
    props: CosmographProps<N, L> & { ref?: Ref<CosmographRef<N, L>> }
  ) => React.ReactElement

export type { CosmographInputConfig }
